
.red{
  color: #E21717;
}
.green{
  color:#00D84A;
}
.yellow{
  color: #DDD101;
}
.SCROLL{
  overflow-y: scroll;
  overflow-x: hidden;
  -ms-overflow-style: none;
  scrollbar-width: none;
  scroll-behavior: smooth;
}
*{
  padding: 0;
  margin: 0;
  border: 0;
  
  font-size: 100%;
  scroll-behavior: smooth;
  box-sizing: border-box;
  -webkit-user-select:none;
    -moz-user-select:none;
    user-select:none;
}

.container{
  width: 100% ;
  display: flex;
  flex-direction: row;
  height: 100vh;
}
.menu{
  width: 15%;
  padding: 10px 0px 0 0px;

}
.menu .top-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.item-left{
  padding-left: 10px;
}
.menu .top-menu span{
  display:flex;
  align-items: center;
}
.logo{
  margin-right: 10px;
  width: 40px;
}
.icon-menu{
  margin-right: 20px;
}
.slogan{
  font-size: 18px;
  font-weight: bold;
}
.catalog{
  margin: 20px 0 0 0;
}
.link{
  text-decoration: none;
  color: #242B2E;
}
ul{
  list-style-type: none;
}
.catalog li{
  font-weight: bold;
  padding: 10px 0 10px 15px;
  display: block;
}
.catalog li:hover{
  background-color: #CAD5E2;
}
.catalog li span{
  margin-left: 15px;
}
.fa-icon-House{
  color: #00a8ff;
}
.fa-icon-Main{
  color: #e74c3c;
}
.fa-icon-Chart{
  color: #f1c40f;
}
.fa-icon-Logs{
  color: #FC427B;
}
/* Content is right */
.content{
  background-color: rgb(242, 239, 239);
  width: 85%;
  display: flex;
  flex-direction: column;
  border-left: 1px solid rgba(211, 211, 211, 0.7);
  height: 100vh;
  overflow: hidden;
}
.content-header{
  padding: 10px 20px 0 20px;
  background-image: linear-gradient(to right, #38aafb, #043dfaf0);
}
.Dashboard{
  color: white;
  display: flex;
  flex-direction: column;
}
.title-content{
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: space-between;
}
.title {
  font-size: 24px;
  margin-bottom: 5px;
  color: white;
}
.profile{
  color: white;
  display: flex;
  align-items: center;
}
.profile span{
  font-size: 14px;
  font-weight: 600;
  padding-right: 15px;
  padding-left: 15px;
  margin: 0;
}
.avatar{
  width: 40px;
  height: 40px;
  object-fit: cover;
  border-radius: 50%;
}
.Dashboard span {
  padding-top: 5px;
  padding-bottom: 5px;
  font-weight: bold;
}
.content-body{
  position: relative;
  height: auto;
}
/* Mainboard */

.Mainboard{
  width: 50%;
  background-color: rgba(0, 0, 0, 0.8);
  position: relative;
  bottom: 50px;
  left: 20px;
  border-radius: 10px;
  color: white;
}
.mainboard-header{
  font-size: 20px;
  color: white;
  height: auto;
  padding: 15px 0 15px 0px;
  background-color: #00D84A;
  display: flex;
  justify-content: center;
  border-radius: 10px;  
}

.mainboard-content{
  padding: 20px auto 20px auto;
}
.value-sensor{
  display: flex;
  justify-content: space-around;
  padding-top: 20px;
}
.item-value .text{
  font-size: 20px;
  color: white;
  font-weight: bold;
}
.content-value {
  display: flex;
  align-items: center;
}
.content-value span{ 
  font-size: 30px;
  font-weight: bold;
  margin-right: 20px;
}

.device-control{
  padding: 20px auto 20px auto;
  font-weight: bold;
  margin-left: 50px;
  margin-top: 20px;
}
.device-group{
  display: flex;
  height: 50px;
}
.device-item{
  margin-top: 10px;
  padding-bottom: 20px;
  display: flex;
  align-items: center;
  margin-right: 40px;
  font-size: 20px;
}
.toggle{
  display: block;
  background-color: #00a8ff;
  width: 40px;
  height: 20px;
  margin-top: 3px;
  margin-left: 10px;
  border-radius: 10px;
  cursor: pointer;
}
.on{
  width: 55%;
  height: 100%;
  display: block;
  background-color:white;
  border-radius: 10px;
}
.off{
  width: 55%;
  height: 100%;
  display: block;
  background-color:#FC427B;
  border-radius: 10px;
  float: right;
}


/* Line Chart */
canvas{

  width:95% !important;
  height:100% !important;
  background-color:#242B2E;
  color: white;
  border-radius: 20px;
  position: relative;
  bottom: 50px;
  left: 20px;
}
.device-infor-group{
  width: 100%;
  display: grid;
  grid-template-columns: 29% 29% 29%;
  grid-gap: 40px;
  margin: 20px 40px;
}
.device-infor{
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 20px;
  background-color: white;
  border-radius: 20px;
  
}
.device-img{
  width: 100px;
}
.device-infor-text{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items:flex-start;
  margin-left: 20px;
  font-weight: bold;
}
.device-infor-name{
  color: #758283;
  margin-bottom: 10px;
}
.device-infor-time{
  margin-left: 10px;
}

/* logs */
.container-table{
  display: flex;
  flex-direction: column;
  padding: 20px;
  background-color: white;
  position:relative;
  bottom: 50px;
  width: 97%;
  border-radius: 20px;
  left: 20px;

}
.search-container{
  display: flex;
  align-items: center;
}
.search-container span{
  font-weight: bold;
}
.search-container input{
  padding: 10px 5px 10px 10px;
  border-radius: 30px;
  border-width: 1px;
  border-style:solid;
  margin: 10px 5px 10px 20px ;
  width: 100px;
  -webkit-transition: width .35s ease-in-out;
  transition: width .35s ease-in-out;
}
.search-container input:focus{
  border-width: 1px;
  width: 200px;
  outline: none;
}
.export{
  display: flex;
  align-items: center;
  cursor: pointer;
  border-width: 2px;
}
.export span{
  font-size: 18px;
  margin-left: 8px;
}
.login-page{
  position:fixed;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  height: 100vh;
  background-image: linear-gradient(to right, #46B2E0, #3944F7);
}
.login-container{
  display: flex;
  flex-direction: row;
  width: 500px;
  padding: 50px 20px;
  justify-content: center;
  align-items: center;
  background-color: rgba(255,255,255,0.8);
  border-radius: 20px;
}
.login-container span{
  color: black;
  font-weight: bold;
}
.login-container input {
  padding: 10px;
  border-radius: 20px;
  margin: 5px 0 5px 0;
  border-width: 2px;
  border-style:solid;
  border-color:#00a8ff;
}
.button-login{
  color: white;
  border-radius: 20px;
  padding: 5px;
  margin-top: 20px;
  padding: 10px 20px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-around;
  background-image:linear-gradient(to right, #46B2E0, #3944F7);
}
.button-login span{
  color: white;
  margin-bottom: 3px;
}
.fa-icon-sortdown {
  align-items: center;
  margin-bottom: 3px;
}
.button-login img {
  width: 30px;
  margin-right: 15px;
}
.logout{
  width: 60%;
  padding: 10px;
  display: flex;
  color: black;
  border-radius: 10px;
  font-weight: bold;
  background-color: white;
  justify-content: center;
  display: none;
}
.profile-container{
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  height: 70px;
}
.profile-container:hover .logout{
  display: flex;
  cursor: pointer;
}
.logo_login{
  width: 200px;
}
.login-group{
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
.login-group span {
  font-size: 20px;
  font-weight: bold;
}
.button_search{
  margin-right: 15px;
  padding: 10px;
  border-radius: 20px;
  background-image: linear-gradient(to right, #1B98F5, #3944F7);
  cursor: pointer;
}